.Die {
    font-size: 10em;
    padding: 0.25em;
    color: slateblue;
}

.shaking {
    animation-name: shake;
    animation-duration: 1s;
}
@keyframes shake {
    10%, 90% {
      transform: translate3d(-2px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(4px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-8px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(8px, 0, 0);
    }
  }